html,
body {
	height: 100%;
	width: 100%;
	padding: 0;
	margin: 0;
	background: #fafafa;
	font-family: "Helvetica Neue", arial, sans-serif;
	font-weight: 400;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	box-sizing: border-box;
}

#app {
	height: 100%;
	display: flex;
	flex-direction: column;
}

main {
	min-height: 100%;
	display: flex;
	flex-direction: column;
	margin-top: 2em;
}

.resource {
	margin-bottom: 1em;
}

@media (min-width: 600px) {
	main {
		width: calc(100% - 240px);
		margin-left: 240px;
	}
	main.nomargin {
		width: 100% !important;
		margin-left: 0px !important;
	}
}
